import firebase from 'firebase';

export interface State {
  orders: Order[];
  loading: boolean;
  user: firebase.User | null;
  updating: boolean;
  error: firebase.auth.Error | null;
  credential: any;
}

export interface Order {
  id: number;
  name: string;
  bubble: Bubble;
  sweetness: Sweetness;
  amount: number;
  owner: {
    uid: string;
    displayName: string;
  };
}

export interface Bubble {
  label: string;
  value: BUBBLE_VALUE;
}
export interface Sweetness {
  label: string;
  value: SWEETNESS_VALUE;
}

export enum SWEETNESS_VALUE {
  NOT,
  LOW,
  NORMAL,
  SWEET,
}

export enum BUBBLE_VALUE {
  NO,
  BLACK,
  SNOW,
  GOLD,
  BROWN,
  FRUITY,
}
