








































import { Component, Vue, Prop } from 'vue-property-decorator';
import { Order, Sweetness, Bubble } from '@/store/state.interface';
import { sweetness, bubble } from '@/database/menu';

@Component
export default class OrderItem extends Vue {
  @Prop()
  public order!: Order;

  @Prop()
  public updating!: Boolean;

  sweetnessList: Sweetness[] = sweetness;

  bubbleList: Bubble[] = bubble;

  inputBubble(value: Bubble) {
    this.$emit('updateOrder', { ...this.order, bubble: value });
  }

  inputSweetness(value: Sweetness) {
    this.$emit('updateOrder', { ...this.order, sweetness: value });
  }

  inputAmount(value: number) {
    if (value >= 0 && value <= 100) {
      this.$emit('updateOrder', { ...this.order, amount: value });
    }
    return false;
  }

  removeOrder(id: number) {
    this.$emit('removeOrder', { id });
  }
}
