



























import { mapState } from 'vuex';
import { Component, Vue } from 'vue-property-decorator';
import store from '@/store/store';
import { State, Order, Bubble } from '@/store/state.interface';
import { bubble, sweetness } from '@/database/menu';

@Component({
  data() {
    return {
      sweetnessList: sweetness,
      bubbleList: bubble,
      columns: [
        {
          field: 'name',
          label: 'ชื่อเมนู',
        },
        {
          field: 'bubble',
          label: 'ไข่มุก',
        },
        {
          field: 'sweetness',
          label: 'ระดับความหวาน',
        },
        {
          field: 'amount',
          label: 'จำนวนแก้ว',
        },
        {
          field: 'owner.displayName',
          label: 'คนสั่ง',
        },
      ],
      username: this.$store.state.username,
    };
  },
  computed: {
    ...mapState({
      orders(state: State): any[] {
        return state.orders.map((order: Order) => ({
          ...order,
          bubble: order.bubble.label,
          sweetness: order.sweetness.label,
        }));
      },
      loading(state: State) {
        return state.loading;
      },
    }),
  },
})
export default class OrderList extends Vue {
  generateText() {
    const { orders } = this.$store.state;
    if (orders.length === 0) {
      this.$buefy.dialog.alert('ไม่มีรายการ');
    } else {
      const text = orders
        .map(
          (each: Order, index: number) => `<li>
            ${'🔸 '}
            <span class="menu-name">${each.name}</span>
            <span class="bubble">${each.bubble.label}</span>
            <span class="sweetness">${each.sweetness.label}</span>
            <span class="amount">${each.amount} แก้ว</span>
            <span class="owner">${each.owner.displayName}</span>
            </li>`,
        )
        .join('');
      this.$buefy.modal.open(`<div class="text-modal"><ul>${text}</ul></div>`);
    }
  }
}
