import Vue from 'vue';
import VueRouter from 'vue-router';

import LoginPage from '@/views/LoginPage.vue';
import MenuPage from '@/views/MenuPage.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/login',
    name: 'login',
    component: LoginPage,
  },
  {
    path: '/',
    name: 'login',
    component: LoginPage,
  },
  {
    path: '/menu',
    name: 'menu',
    component: MenuPage,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
