import firebase from 'firebase';
import Vue from 'vue';
import Vuex, { Action } from 'vuex';
import { firebaseAction, vuexfireMutations } from 'vuexfire';

import db from '@/firebase/db';

import { Order, State } from './state.interface';

Vue.use(Vuex);

const bindOrders: Action<State, State> = firebaseAction(
  async ({ state, bindFirebaseRef, commit }) => {
    await bindFirebaseRef('orders', db.ref('orders'));
    commit('SET_LOADING', false);
  },
);

const addOrder: Action<State, State> = firebaseAction(
  async ({ state, commit }, payload: Order) => {
    commit('SET_UPDATING', true);
    await db.ref(`orders/${payload.id}`).set(payload);
    commit('SET_UPDATING', false);
  },
);

const updateOrder: Action<State, State> = firebaseAction(
  async ({ state, commit }, payload: Order) => {
    commit('SET_UPDATING', true);
    await db.ref(`orders/${payload.id}`).update(payload);
    commit('SET_UPDATING', false);
  },
);

const removeOrder: Action<State, State> = firebaseAction(
  async ({ state, commit }, id: number) => {
    commit('SET_UPDATING', true);
    await db.ref(`orders/${id}`).remove();
    commit('SET_UPDATING', false);
  },
);

const authorized: Action<State, State> = async (
  { commit },
  payload: firebase.User,
) => {
  commit('SET_USER', payload);
  commit('SET_ERROR', null);
};

const unauthorized: Action<State, State> = async ({ commit }) => {
  commit('UNSET_USER');
  commit('SET_ERROR', null);
};

const authError: Action<State, State> = async (
  { commit },
  payload: firebase.auth.Error,
) => {
  commit('SET_ERROR', payload);
};

const loginCompleted: Action<State, State> = async (
  { commit },
  payload: Object,
) => {
  commit('SET_CREDENTIAL', payload);
};

const loginFailed: Action<State, State> = async ({ commit }) => {
  commit('SET_CREDENTIAL', null);
};

const store = new Vuex.Store({
  state: {
    orders: [],
    loading: true,
    user: null,
    updating: false,
    error: null,
    credential: null,
  } as State,
  mutations: {
    ...vuexfireMutations,
    addOrder(state: State, order: Order) {
      state.orders.push(order);
    },
    SET_LOADING(state: State, loading: boolean) {
      state.loading = loading;
    },
    SET_UPDATING(state: State, updating: boolean) {
      state.updating = updating;
    },
    SET_USER(state: State, user: firebase.User) {
      state.user = user;
    },
    UNSET_USER(state: State) {
      state.user = null;
    },
    SET_ERROR(state: State, error: firebase.auth.Error) {
      state.error = error;
    },
    SET_CREDENTIAL(state: State, credential: any) {
      state.credential = credential;
    },
  },
  actions: {
    bindOrders,
    addOrder,
    updateOrder,
    removeOrder,
    authorized,
    unauthorized,
    authError,
    loginCompleted,
    loginFailed,
  },
  modules: {},
  getters: {
    cartSize: (state: State) => {
      const { user } = state;
      if (user) {
        const { uid } = user as firebase.User;
        return state.orders.length;
      }
      return 0;
    },
    userCartSize: (state: State) => {
      const { user } = state;
      if (user) {
        const { uid } = user as firebase.User;
        return state.orders.filter((o: Order) => o.owner.uid === uid).length;
      }
      return 0;
    },
  },
});

export default store;
