













import { Component, Vue } from 'vue-property-decorator';
import { mapState } from 'vuex';
import firebase from 'firebase/app';
import TopNav from './components/TopNav.vue';
import ui from '@/firebase/firebase-ui';
import store from '@/store/store';
import { State } from './store/state.interface';

@Component({
  components: {
    TopNav,
  },
  computed: {
    ...mapState({
      user: (state: State) => state.user,
      loading: (state: State) => state.loading,
    }),
  },
  created() {
    store.dispatch('bindOrders');
  },
  mounted() {
    const { currentUser } = firebase.auth();
    if (currentUser) {
      store.dispatch('authorized', currentUser);
    }
    firebase.auth().onAuthStateChanged(
      (user: any) => {
        if (user) {
          store.dispatch('authorized', user);
          this.$router.push('/menu');
        } else {
          store.dispatch('unauthorized');
          this.$router.push('/');
        }
      },
      (error: firebase.auth.Error) => {
        store.dispatch('authError', error);
        this.$buefy.dialog.alert('เกิดข้อผิดพลาดบางอย่าง');
      },
    );
  },
})
export default class App extends Vue {}
