



















































import { Component, Vue } from 'vue-property-decorator';
import { mapState, Store, mapGetters } from 'vuex';
import store from '@/store/store';
import { Order, State } from '@/store/state.interface';
import {
  newMenu,
  milkTea,
  blackTea,
  thaiTea,
  soda,
  otherDrink,
  sweetness,
  bubble,
} from '@/database/menu';
import DrinkItem from '@/interfaces/DrinkItem';
import DrinkList from '@/components/DrinkList.vue';
import OrderList from '@/components/OrderList.vue';
import AddedOrderList from '@/components/AddedOrderList.vue';

@Component({
  components: {
    DrinkList,
    OrderList,
    AddedOrderList,
  },
  data() {
    return {
      newMenu,
      milkTea,
      blackTea,
      thaiTea,
      soda,
      otherDrink,
      orders: this.$store.state.orders,
    };
  },
  computed: {
    ...mapGetters(['cartSize', 'userCartSize']),
    ...mapState({
      user(state: State) {
        return state.user;
      },
    }),
  },
  methods: {
    addOrder(drink: DrinkItem) {
      const { user } = this.$store.state;
      const owner = {
        uid: user.uid,
        displayName: user.displayName,
      };
      const order = {
        id: new Date().getTime(),
        name: drink.name,
        bubble: bubble[0],
        sweetness: sweetness[0],
        amount: 1,
        owner,
      } as Order;
      store.dispatch('addOrder', order);
    },
    updateOrder(payload: Order) {
      const { user } = this.$store.state;
      const owner = {
        uid: user.uid,
        displayName: user.displayName,
      };
      store.dispatch('updateOrder', { ...payload, owner });
    },
    removeOrder(e) {
      store.dispatch('removeOrder', e.id);
    },
  },
})
export default class MenuPage extends Vue {}
